.main-nav {
  position: relative;
}

.main-nav--opened  {
  display: grid;
  grid-template-rows: min-content auto;
}

.main-nav__top {
  display: grid;
  grid-template-columns: min-content auto;
  align-items: center;
  column-gap: 50px;

  min-height: 80px;
  .wrapper();

  @media (min-width: @tablet-width) {
    grid-template-columns: min-content 1fr auto;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr min-content 1fr;

    min-height: 90px;
  }
}

.main-nav__site-toggle {
  @media (min-width: @desktop-width) {
    display: none;
  }
}

.main-nav__top-contacts {
  @media (max-width: (@desktop-width - 0.2px)) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    margin-top: 23px;
  }
}

.main-nav__logo {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
    width: 192px;
    height: 63px;

    .interactive-styles-custom();
  }
}

.main-nav__logo img {
  width: 100%;
  height: 100%;
}

.main-nav__top-search-form {
  display: none;

  @media (min-width: @tablet-width) {
    display: block
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.main-nav__user-block {
  justify-self: end;

  margin-top: 6px;
  margin-right: 12px;

  @media (min-width: @tablet-width) {
    margin-top: 0;
    margin-right: 0;
  }

  @media (min-width: @desktop-width) {
    margin-top: 5px;
  }
}

.main-nav__user-list {
  display: flex;
  gap: 50px;
}

.main-nav__favorites {
  @media (max-width: (@mobile-width - 0.2px)) {
    display: none;
  }
}

.main-nav__private {
  @media (max-width: (@mobile-width - 0.2px)) {
    display: none;
  }
}

.main-nav__bottom {
  position: relative;

  display: none;
  .wrapper();

  color: @color-font-white;
  background-color: @color-back-black-01;

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: min-content 1fr auto;
    gap: 60px;
    align-items: center;
    padding-top: 13px;
    padding-bottom: 12px;

    .border-decor-triangle--top(6px, 6px, 1px, @color-dark-orange);
  }
}

.main-nav--opened .main-nav__bottom {
  @media (max-width: (@desktop-width - 0.2px)) {
    position: absolute;
    top: 0;
    left:0;
    z-index: 100;

    grid-column: 1 / -1;
    grid-row: 2 / -1;

    display: grid;
    gap: 20px;

    box-sizing: border-box;
    width: 100%;
    // padding-top: 25px;
    // padding-bottom: 40px;
    padding: 25px 0 40px;

    .border-decor-triangle--top(6px, 6px, 1px, @color-dark-orange);
  }
}

.main-nav--opened .main-nav__bottom-search-form {
  display: block;

  @media (min-width: @tablet-width) {
    display: none
  }
}

.main-nav__site-list {
  display: flex;
  gap: 60.6px;
  flex-wrap: wrap;
}

.main-nav--opened .main-nav__site-list {
  @media (max-width: (@desktop-width - 0.2px)) {
    display: inherit;
    gap: inherit;
  }
}

.main-nav__bottom-contacts {
  display: none;
}

.main-nav--opened .main-nav__bottom-contacts {
  display: block;
}
